<template>
  <div class="card shadow" :class="type === 'dark' ? 'bg-default': ''">
    <div class="card-header border-0" :class="type === 'dark' ? 'bg-transparent': ''">
      <div class="row align-items-center">
        <div class="col">
          <h3 class="mb-0" :class="type === 'dark' ? 'text-white': ''">{{title}}</h3>
        </div>
      </div>
    </div>

    <div class="table-responsive">
      <base-table
        class="table align-items-center table-flush"
        :class="type === 'dark' ? 'table-dark': ''"
        :thead-classes="type === 'dark' ? 'thead-dark': 'thead-light'"
        tbody-classes="list"
        :data="data.slice(beginIndex, endIndex)"
        :columns="['Item', 'Charges', 'Price', 'Status', 'From', 'Current', 'To', 'Completion', '']"
        emptyMessage="You don't have any package at the moment"
      >
        <template slot-scope="{row}">
          <th scope="row">
            <div class="media align-items-center">
              <span class="avatar rounded-circle mr-3">
                <img v-if="row.img" :src="row.img" alt="Image placeholder" />
                <i v-else class="ni ni-cart"></i>
              </span>
              <div class="media-body">
                <span class="name mb-0 text-sm">{{row.name}}</span>
              </div>
            </div>
          </th>
          <td class="budget">{{row.charge}}</td>
          <td class="budget">{{row.price}}</td>
          <td>
            <badge class="badge-dot mr-4" :type="statusType(row.status)">
              <i :class="`bg-${statusType(row.status)}`"></i>
              <span class="status">{{row.status}}</span>
            </badge>
          </td>

          <td class="budget">{{row.from.loc}}</td>
          <td class="budget">{{row.loc}}</td>
          <td class="budget">{{row.to.loc}}</td>
          <td>
            <div class="d-flex align-items-center">
              <span class="completion mr-2">{{row.completion}}%</span>
              <div>
                <base-progress
                  :type="statusType(row.status)"
                  :show-percentage="false"
                  class="pt-0"
                  :value="row.completion"
                />
              </div>
            </div>
          </td>

          <td class="text-right">
            <base-dropdown class="dropdown" position="right">
              <a
                slot="title"
                class="btn btn-sm btn-icon-only d-inline-flex flex-column justify-content-center align-items-center"
                role="button"
                data-toggle="dropdown"
                aria-haspopup="true"
                aria-expanded="false"
              >
                <strong class="dot"></strong>
                <strong class="dot"></strong>
                <strong class="dot mb-0"></strong>
              </a>
              <template>
                <a
                  @click.prevent="$notify('This is only available to PRO members')"
                  class="dropdown-item"
                  href="#"
                >
                  <i class="ni ni-square-pin text-danger"></i>
                  <span>View on Map</span>
                </a>
              </template>
            </base-dropdown>
          </td>
        </template>
      </base-table>
    </div>

    <div
      class="card-footer d-flex justify-content-end"
      :class="type === 'dark' ? 'bg-transparent': ''"
    >
      <base-pagination
        :total="data.length"
        :per-page="perPage"
        :value="currentPage"
        v-on:input="paginate"
      ></base-pagination>
    </div>
  </div>
</template>
<script>
export default {
  name: "packages-table",
  props: {
    type: {
      type: String
    },
    title: String,
    data: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  data: () => {
    return {
      currentPage: 1,
      perPage: 5,
      beginIndex: 0,
      endIndex: 5
    };
  },
  methods: {
    paginate(page) {
      this.currentPage = page;
      this.beginIndex = (this.currentPage - 1) * this.perPage;
      this.endIndex = this.beginIndex + this.perPage;
    },
    statusType(status) {
      switch (status) {
        case "pending":
          return "warning";
        case "completed":
          return "success";
        case "delayed":
          return "danger";
        default:
          return "info";
      }
    }
  }
};
</script>
<style>
.dot {
  border-radius: 50%;
  width: 4px;
  height: 4px;
  margin-bottom: 2px;
  background-color: #525f7f;
}
</style>
